export var navigateToPageSection = function navigateToPageSection() {
  var hash = window.location.hash;
  if (hash === "") return;
  var element = document.querySelector(hash);
  if (!element) return;
  element.scrollIntoView();
};
export var navigateToPageSectionOnLoad = function navigateToPageSectionOnLoad() {
  addEventListener("load", function () {
    return navigateToPageSection();
  });
};